/** Media Queries **/
.servicios .text-center {
  text-align: center;
}

.seccion h2 {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicios {
  background-color: #FFFFFF;
  transition: all 300ms ease;
  border-radius: 1rem;
  text-align: center;
  font-weight: 700;
}
.listado-servicios .servicios.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicios.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicios:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.listado-servicios .servicios p {
  color: #1a1b15;
}
.listado-servicios .servicios .precio-servicio {
  color: #0da6f3;
}